import React, { useState } from 'react';
import { Eye, EyeOff, Send, QrCode } from 'lucide-react';
import './style.scss';
import { cn } from '@/utils/utils';
import { userStore } from '@/store/user';
import { useWallet } from '@/hooks/useWallet';

interface LoginModalProps {
  isOpen: boolean;
  isAnimation: boolean;
  onClose: () => void;
  onSwitchToRegister: () => void;
}

const LoginModal: React.FC<LoginModalProps> = ({ isOpen, onClose, onSwitchToRegister, isAnimation }) => {
  const [showPassword, setShowPassword] = useState(false);
  const {setLoginStatus, setToken} = userStore();
  const {connect} = useWallet();
  const [formData, setFormData] = useState({
    email: '',
    password: ''
  });

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log(':', formData);
    setLoginStatus(true);
    onClose();
  };

  
  if (!isOpen) return null;

  const handleConnect = async () => {

    await connect();
    setLoginStatus(true)
    setToken('123456')
    onClose();
  }

  return (
    <div className={cn('login-modal-overlay', { 'not-animation': !isAnimation })}  onClick={onClose}>
      <div className={cn('login-modal', { 'not-animation': !isAnimation })} onClick={(e) => e.stopPropagation()}>
        <div className="login-modal__header">
          <h2 className="login-modal__title"></h2>
          <button 
            className="login-modal__close-btn"
            onClick={onClose}
            type="button"
          >
            
          </button>
        </div>

        <div className="login-modal__content">
          <form onSubmit={handleSubmit} className="login-form">
            <div className="register-link">
              
              <button type="button" className="link-btn" onClick={onSwitchToRegister}>
                
              </button>
            </div>

            <div className="form-group">
              <label className="form-label"></label>
              <div className="input-wrapper">
                <input
                  type="email"
                  name="email"
                  placeholder=""
                  value={formData.email}
                  onChange={handleInputChange}
                  className="form-input"
                  required
                />
              </div>
            </div>

            <div className="form-group">
              <label className="form-label"></label>
              <div className="input-wrapper">
                <input
                  type={showPassword ? 'text' : 'password'}
                  name="password"
                  placeholder=""
                  value={formData.password}
                  onChange={handleInputChange}
                  className="form-input"
                  required
                />
                <button
                  type="button"
                  className="password-toggle"
                  onClick={() => setShowPassword(!showPassword)}
                >
                  {showPassword ? <EyeOff size={18} /> : <Eye size={18} />}
                </button>
              </div>
            </div>

            <div className="forgot-password">
              <button type="button" className="forgot-btn">
                
              </button>
            </div>

            <button type="submit" className="submit-btn">
              
            </button>
          </form>

          <div className="social-login">
            <div className="divider">
              <span></span>
            </div>
            
            <div className="social-buttons">
              <button className="social-btn">
                <div className="social-icon telegram">
                  <Send size={20} />
                </div>
                <span className="social-label">Telegram</span>
              </button>
              
              <button className="social-btn">
                <div className="social-icon phantom">
                  
                </div>
                <span className="social-label">Phantom</span>
              </button>
              
              <button className="social-btn">
                <div className="social-icon qrcode">
                  <QrCode size={20} />
                </div>
                <span className="social-label">APP</span>
              </button>
            </div>
          </div>

          <div className="bottom-links">
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <a className="wallet-link" onClick={handleConnect}>
               <span className="arrow"></span>
            </a>
            
            <div className="footer-links">
              {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
              <a href="#" className="footer-link"></a>
              <span style={{color: '#333'}}>|</span>
              {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
              <a href="#" className="footer-link"></a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginModal;